@import url(https://fonts.googleapis.com/css?family=Arimo:400,400italic|Playfair+Display+SC:400,700&subset=latin,cyrillic);
.Card {
  position: relative;
  z-index: 0;
  width: 436px;
  height: 596px;
  border-radius: 10px;
  overflow: hidden;
  margin: 30px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 24px;
  font-family: 'Roboto Condensed', sans-serif;
}

.Card::before {
  content: '';
  position: absolute;
  z-index: -2;
  left: -50%;
  top: -50%;
  width: 200%;
  height: 200%;
  background-color: #788b9a;
  background-repeat: no-repeat;
  background-size: 50% 50%, 50% 50%;
  background-position: 0 0, 100% 0, 100% 100%, 0 100%;
  background-image: linear-gradient(#788b9a, #788b9a), linear-gradient(#3c3e48, #3c3e48),
    linear-gradient(#788b9a, #788b9a), linear-gradient(#3c3e48, #3c3e48);
  -webkit-animation: anim-Card 8s linear infinite;
          animation: anim-Card 8s linear infinite;
}
.Card::after {
  content: '';
  position: absolute;
  z-index: -1;
  left: 5px;
  top: 5px;
  width: calc(100% - 10px);
  height: calc(100% - 9px);
  background: white;
  border-radius: 5px;
}
@-webkit-keyframes anim-Card {
  100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
  }
}
@keyframes anim-Card {
  100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
  }
}

